@import "main-bx";


.fancybox__container {
    .fancybox__backdrop {
        background-color: rgba(0, 0, 0, 0.60)
    }
}

.modal {
    display: none;

    &_stock {
        &.fancybox__content {
            display: none;
            padding: 40px;
            width: 100%;
            max-width: 1000px;

            @include below(768px) {
                padding: 30px;
            }

            @include below(575px) {
                padding: 20px;
            }

            .f-button {
                width: 24px;
                height: 24px;
                top: 15px !important;
                right: 15px !important;

                @include below(580px) {
                    top: 10px !important;
                    right: 10px !important;

                    &.is-close-btn {
                        background-color: transparent;
                        border-radius: 0;
                    }

                    
                
                }
                svg {
                    color: #626970;
                }
            }
        }
    }

    &_corporate {
        &.fancybox__content {
            display: none;
            padding: 40px;
            width: 100%;
            max-width: 1500px;

            @include below(768px) {
                padding: 30px;
            }

            @include below(575px) {
                padding: 20px;
            }

            .f-button {
                width: 24px;
                height: 24px;
                top: 15px !important;
                right: 15px !important;

                @include below(580px) {
                    top: 10px !important;
                    right: 10px !important;

                    &.is-close-btn {
                        background-color: transparent;
                        border-radius: 0;
                    }

                    
                
                }
                svg {
                    color: #626970;
                }
            }
        }
    }

    
    &_services {

        &.fancybox__content {
            display: none;
            padding: 40px;
            width: 100%;
            max-width: 720px;

            @include below(768px) {
                padding: 30px;
            }

            @include below(575px) {
                padding: 20px;
            }

            .f-button {
                width: 24px;
                height: 24px;
                top: 15px !important;
                right: 15px !important;

                @include below(580px) {
                    top: 10px !important;
                    right: 10px !important;

                    &.is-close-btn {
                        background-color: transparent;
                        border-radius: 0;
                    }

                    
                
                }
                svg {
                    color: #626970;
                }
            }
        }
        
    }

    &__title {
        font-size: 35px;
        font-style: normal;
        font-weight: 500;
        line-height: 120%;
        text-transform: uppercase;
        margin-bottom: 32px;

        @include below(1024px) {
            font-size: 28px;
        }

        @include below(768px) {
            font-size: 25px;
        }

        @include below(575px) {
            font-size: 20px;
            margin-bottom: 30px;
        }
    }

    &__text {
        font-size: 14px;
        font-weight: 400;
        line-height: 120%;
        margin-bottom: 20px; 

        span {
            text-transform: uppercase;
        }

        @include below(575px) {
            font-size: 12px;
            margin-bottom: 30px;
        }
    }


    &__wrap {
        margin-top: 40px;
        &-title {
            font-size: 22px;
            font-weight: 500;
            line-height: 120%; 
            text-transform: uppercase;
            margin-bottom: 20px;

            @include below(1024px) {
                font-size: 20px;
            }

            @include below(768px) {
                font-size: 18px;
            }

            @include below(575px) {
                font-size: 16px;
                margin-bottom: 16px;
            }
        }   

        &-text {
            font-size: 14px;
            font-weight: 400;
            line-height: 120%;
            margin-bottom: 20px;

            @include below(575px) {
                font-size: 12px;
                margin-bottom: 16px;
            }
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        margin: 0 -30px;

        @include below(1024px) {
            flex-wrap: wrap;
            margin: 0;
        }
    }

    &__col {
        width: calc(50% - 60px);
        margin: 0 30px;

        @include below(1024px) {
            width: 100%;
            margin: 0;

            &:not(:first-child) {
                margin-top: 24px;

                @include below(575px) {
                    margin-top: 12px;
                }
            }
        }
    }

    &__item {
        display: flex;
        align-items: start;
        &:not(:last-child) {
            margin-bottom: 24px;

            @include below(575px) {
                margin-bottom: 12px;
            }
        }
        svg {
            margin-top: -4px;
            margin-right: 20px;
            width: 24px;
            height: 24px;
            flex-shrink: 0;

            @include below(768px) {
                margin-right: 8px;
            }
        }

        p {
            font-size: 14px;
            font-weight: 400;
            line-height: 120%; 

            @include below(575px) {
                font-size: 12px;
            }

            strong {
                display: block;
                margin-bottom: 16px;
                font-size: 18px;
                font-weight: 500;
                line-height: 120%;
                text-transform: uppercase;

                @include below(768px) {
                    font-size: 16px;
                }

                @include below(575px) {
                    font-size: 12px;
                    margin-bottom: 8px;
                }
            }
        }
    }


    &__btn {
        margin-top: 40px;
        padding: 20px 50px 20px 42px;

        @include below(540px) {
            width: 100%;
            padding: 20px 15px;
            margin-top: 30px;
        }
    }

    &__box {
        &-title {
            font-size: 24px;
            font-weight: 500;
            line-height: 120%;
            text-transform: uppercase;
            margin-bottom: 24px;

            @include below(992px) {
                font-size: 20px;
                margin-bottom: 20px;
            }
        }

        &-text {
            font-size: 16px;
            font-weight: 400;
            line-height: 120%;
            margin-bottom: 40px;

            @include below(992px) {
                font-size: 14px;
                margin-bottom: 20px;
            }
        }
    }

    &__form {

        &-label {
            display: block;
            position: relative;
            width: 100%;

            &:first-child {
                margin-bottom: 10px;
            }

            &:nth-child(2) {
                margin-bottom: 20px;
            }

            &:nth-child(3) {
                display: inline-flex;
                align-items: center;
                cursor: pointer;

                @include below(590px) {
                    align-items: start;
                }

                input {
                    position: absolute;
                    top: 0;
                    left: -1000%;

                    &:checked ~ .modal__form-mark {
                        svg {
                            opacity: 1;
                        }
                    }
                }
            }
        }

        &-mark {
            flex-shrink: 0;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: 16px;
            height: 16px;
            border: 1px solid #000;
            margin-right: 10px;

            svg {
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: opacity .3s linear;
            }
        }

        &-policy {
            color: #626970;
            font-size: 12px;
            font-weight: 400;
            line-height: normal;

            a {
                color: #626970;
                text-decoration: underline;
            }
        }

        &-input {
            width: 100%;
            height: 54px;
            padding: 15px 20px;
            font-size: 14px;
            font-weight: 400;
            line-height: normal;
            border: 1px solid #CCCCCD;
            transition: border-color .3s linear;

            &:focus,
            &:hover {
                border-color: $black_color;
            }
        }

        &-btn {
            margin-top: 40px;
            width: 100%;

            @include below(575px) {
                margin-top: 20px;
            }
        }
    }

    &__tabs {
        margin-bottom: 20px;

        &-list {
            display: flex;

            @include below(710px) {
                overflow: hidden;
                overflow-x: auto;
                padding-bottom: 5px;
            }
        }

        &-item {
            width: 25%;
            &:not(:last-child) {
                margin-right: 2px;
            }

            @include below(710px) {
                width: fit-content;
            }
        }

        &-btn {
            width: 100%;
            padding: 14px 32px;
            font-size: 14px;
            font-weight: 500;
            line-height: 120%; 
            white-space: nowrap;
            text-transform: uppercase;
            text-align: center;
            color: #626970;
            background-color: #F8F8F8;
            border-bottom: 2px solid transparent;
            transition: border-color .3s linear, color .3s linear;

            &:hover,
            &:active {
                border-color: #000;
                color: #121314;
            }

            &_active {
                border-color: #000;
                color: #121314;
            }
        }
    }
}