@import "main-bx";

.services {
    padding: 20px 0 150px;
    @include below(1380px) {
        padding: 20px 0 120px;
    }

    @include below(1240px) {
        padding: 20px 0 90px;
    } 

    @include below(768px) {
        padding: 20px 0 60px;
    } 

    @include below(574px) {
        padding: 20px 0 40px;
    } 

    &__wrap {
        display: flex;
        align-items: stretch;
        margin: 0 -10px;

        @include below(1380px) {
            flex-wrap: wrap;
            margin: -20px -10px 0;
        }

        @include below(575px) {
            margin: -6px 0 0;
        }
    }

    &__item {
        width: calc(25% - 20px);
        margin: 0 10px;
        display: flex;
        flex-direction: column;
        padding: 30px;
        background-color: #F8F8F8;

        @include below(1380px) {
            width: calc(50% - 20px);
            margin: 20px 10px 0;
            padding: 20px;
        }

        @include below(575px) {
            width: 100%;
            margin: 6px 0 0;
            flex-direction: row;
            align-items: center;
        }
    }

    &__icon {
        flex-shrink: 0;
        margin-bottom: 22px;
        color: $black_color;

        @include below(575px) {
           margin: 0 30px 0 0;
        }
        
        svg {
            width: 60px;
            height: 60px;

            @include below(1380px) {
                width: 50px;
                height: 50px;
            }

            @include below(768px) {
                width: 40px;
                height: 40px;
            }
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
        align-items: start;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 120%;
        text-transform: uppercase;
        color: $black_color;

        @include below(1480px) {
            font-size: 16px;
        }

        @include below(768px) {
            font-size: 14px;
        }

        span {
            &:first-child {
                color: $accent_color;
            }
        }
    }
}