@import "main-bx";

.management {
    padding: 75px 0 150px;

    @include below(1240px) {
        padding: 40px 0 120px;
    }

    @include below(768px) {
        padding: 30px 0 80px;
    }

    @include below(575px) {
        padding: 30px 0 60px;
    }

    &__title {
        font-weight: 500;
        font-size: 35px;
        line-height: 120%; 
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 40px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(1024px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 20px;
            text-align: start;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        margin: 0 -10px;

        @include below(1024px) {
            flex-wrap: wrap;
            margin: -30px 0 0;
        }

        @include below(768px) {
            margin: -20px 0 0;
        }
    }

    &__item {
        width: calc(50% - 20px);
        margin: 0 10px;
        display: flex;
        flex-direction: column;
        align-items: start;
        background-color: #F8F8F8;
        padding: 30px;
        position: relative;

        @include below(1024px) {
            width: 100%;
            margin: 30px 0 0;
        }

        @include below(768px) {
            padding: 20px;
            margin: 20px 0 0;
        }
    }

    &__icon {
        position: absolute;
        top: 0;
        right: 60px;

        svg {
            width: 61px;
            height: 54px;
        }

        @include below(1380px) {
            right: 20px;

            svg {
                width: 54px;
                height: 44px;
            }
        }

        @include below(590px) {
            svg {
                width: 36px;
                height: 32px;
            }
        }
    }

    &__name {
        font-size: 24px;
        font-weight: 500;
        line-height: 120%; 
        text-transform: uppercase;
        margin-bottom: 14px;

        @include below(1240px) {
            font-size: 20px;
        }

        @include below(768px) {
            font-size: 18px;
        }

        @include below(590px) {
            font-size: 16px;
        }
    }

    &__text {
        font-size: 14px;
        font-weight: 400;
        line-height: 120%;
        margin-bottom: 40px;

        span {
            text-transform: uppercase;
        }

        @include below(1024px) {
            br {
                display: none;   
            }
        }

        @include below(768px) {
            margin-bottom: 32px;
        }

        @include below(590px) {
            font-size: 12px;
        }
    }

    &__tel {
        font-size: 20px;
        font-weight: 300;
        line-height: normal;
        margin-bottom: 60px;

        a {
            color: $black_color;
            transition: text-decoration .3s linear;

            &:hover {
                text-decoration: underline;
            }
        }

        @include below(1240px) {
            font-size: 18px;
        }

        @include below(768px) {
            margin-bottom: 32px;
        }

        @include below(590px) {
            font-size: 16px;
        }
        
    }

    &__button {
        padding: 15px 92px;
        margin-top: auto;

        @include below(1240px) {
            padding: 15px 65px;
        }

        @include below(540px) {
            width: 100%;
            padding: 20px 15px;
        }
    }
}