@import "main-bx";

.benefits {
    position: relative;

    &__wrap {
        position: absolute;
        top: 185px;
        left: 210px;
        color: $white_color;

        @include below(1780px) {
            top: 50px;
            left: 100px;
        }

        @include below(1440px) {
            position: static;
            color: $black_color;
            padding: 60px 0 120px;
        }

        @include below(768px) {
            padding: 20px 0 60px;
        }
    }

    &__title {
        font-size: 35px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 40px;

        @include below(1540px) {
            margin-bottom: 20px;
        }

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(1024px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 20px;
            text-align: start;
        }
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        max-width: 700px;
        min-width: 700px;
        border: 1px solid #D9D9D9;

        @include below(1540px) {
            top: 50px;
            left: 100px;
            max-width: 600px;
            min-width: 600px;
        }

        @include below(1440px) {
            max-width: initial;
            min-width: initial;
        }

        @include below(1240px) {
            border: none;
        }
    }

    &__item {
        padding: 20px 20px 30px 30px;
        display: flex;
        flex-direction: column;
        width: 50%;

        @include below(1440px) {
            width: 25%;
        }

        @include below(1240px) {
            width: 33.333%;
            border: 1px solid #D9D9D9;
        }

        @include below(1024px) {
            width: 50%;
        }

        @include below(590px) {
            width: 100%;
            padding: 20px;
        }

        &:nth-child(1) {
            order: 1;
            border-right: 1px solid #D9D9D9;
            border-bottom: 1px solid #D9D9D9;

        }

        &:nth-child(2) {
            order: 3;
            border-right: 1px solid #D9D9D9;
            border-bottom: 1px solid #D9D9D9;

            @include below(1440px) {
                order: 2;
            }
 
        }

        &:nth-child(3) {
            order: 5;
            border-right: 1px solid #D9D9D9;
            border-bottom: 1px solid #D9D9D9;

            @include below(1440px) {
                order: 3;
            }
        }

        &:nth-child(4) {
            order: 7;
            border-right: 1px solid #D9D9D9;

            @include below(1440px) {
                order: 4;
                border-right: none;
                border-bottom: 1px solid #D9D9D9;
            }

            @include below(1024px) {
                border-right: 1px solid #D9D9D9;
            }
        }

        &:nth-child(5) {
            order: 2;
            border-bottom: 1px solid #D9D9D9;

            @include below(1440px) {
                order: 5;
                border-right: 1px solid #D9D9D9;
                border-bottom: none;
            }
        }

        
        &:nth-child(6) {
            order: 4;
            border-bottom: 1px solid #D9D9D9;
            @include below(1440px) {
                order: 6;
                border-right: 1px solid #D9D9D9;
                border-bottom: none;
            }

            @include below(1240px) {
                border-bottom: 1px solid #D9D9D9;
            }

        
        }

        &:nth-child(7) {
            order: 6;
            border-bottom: 1px solid #D9D9D9;

            @include below(1440px) {
                order: 6;
                border-right: 1px solid #D9D9D9;
                border-bottom: none;
            }

            @include below(1240px) {
                border-bottom: 1px solid #D9D9D9;
            }

        }

        &:nth-child(8) {
            order: 8;
        }
    }

    &__number {
        font-size: 12px;
        font-weight: 400;
        line-height: 120%; 
        margin-left: auto;

    }

    &__item-title {
        font-size: 16px;
        font-weight: 500;
        line-height: 120%; 
        text-transform: uppercase;
        margin: 40px 0 10px;

        @include below(590px) {
            margin: 20px 0 10px;
        }
    }

    &__item-text {
        font-size: 12px;
        font-weight: 400;
        line-height: 120%;
    }
}