@import "main-bx";

.intro {
    position: relative;


    &__content {
        position: absolute;
        top: 205px;
        left: 210px;
        color: $white_color;

        @include below(1700px) {
            top: 180px;
            left: 100px;
        }

        @include below(1240px) {
           top: 150px;
        }

        @include below(992px) {
            padding: 40px 0;
            position: static;
            color: $black_color;
        }

        
        @include below(590px) {
            padding: 20px 0;
        }
    }

    &__suptitle {
        font-size: 32px;
        font-weight: 300;
        line-height: normal;
        letter-spacing: 41.92px;
        text-transform: uppercase;
        margin-bottom: 23px;

        @include below(1380px) {
            font-size: 26px;
            margin-bottom: 15px;
        }

        @include below(1024px) {
            font-size: 20px;
        }

        @include below(575px) {
            font-size: 12px;
            letter-spacing: 15.72px;
        }
    }

    &__title {
        font-size: 80px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;

        @include below(1540px) {
            font-size: 70px;
        }

        @include below(1380px) {
            font-size: 50px;
        }

        @include below(1140px) {
            font-size: 40px;
        }

        @include below(992px) {
            font-size: 30px;
        }

        @include below(590px) {
            font-size: 24px;

            br {
                display: none;
            }
        }
    }

    &__text {
        margin-top: 40px;
        font-weight: 300;
        font-size: 32px;
        line-height: 38px;

        @include below(1540px) {
            font-size: 28px;
            line-height: 30px;
        }
   
        @include below(1380px) {
            font-size: 22px;
            margin-top: 20px;
        }

        
        @include below(992px) {
            br {
                display: none;
            }
        }

        @include below(590px) {
            font-size: 16px;
            line-height: 20px;

            br {
                display: block;
            }
        }
    }

    &__btn {
        margin-top: 60px;
        padding: 15px 79px 20px;

        @include below(1380px) {
            margin-top: 40px;
        }

        @include below(540px) {
            margin-top: 20px;
            padding: 15px 15px 20px;
            width: 100%;
        }

    }
}