@import "main-bx";

.stock {
    padding: 0 0 150px;

    @include below(1380px) {
        padding: 0 0 120px;
    }

    @include below(1240px) {
        padding: 0 0 90px;
    } 

    @include below(768px) {
        padding: 0 0 60px;
    } 

    &.stock {
        &_mileage {
            padding: 50px 0 150px;

            @include below(1240px) {
                padding: 35px 0 100px;
            }

            @include below(575px) {
                padding: 20px 0 60px;
            } 
        }
    }

    &__title {
        font-size: 35px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 40px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(1024px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 20px;
            text-align: start;
        }
    }

    &__wrap {
        display: flex;
        flex-direction: column;
    }

    &__form {
        margin-bottom: 40px;

        @include below(768px) {
           margin-bottom: 20px;
        }
    }

    &__filter {
        display: flex;
        align-items: stretch;
        margin: 0 -10px;

        @include below(1240px) {
            flex-wrap: wrap;
            margin: -20px -10px 0;
        }

        @include below(625px) {
            margin: -20px 0 0;
        }

        &_second {

            @include below(1440px) {
                flex-wrap: wrap;
                margin: -20px -10px 0;
            }

            @include below(625px) {
                margin: -20px 0 0;
            }
            
            .stock__filter-item {
                display: block;
                width: calc(20% - 20px);
                margin: 0 10px;

                @include below(1440px) {
                    width: calc(33.333% - 20px);
                    margin: 20px 10px 0;
                }

                @include below(1024px) {
                    width: calc(50% - 20px);
                    margin: 20px 10px 0;
                }

                @include below(625px) {
                    width: 100%;
                    margin: 20px 0 0;
                    &:nth-child(1) {
                        order: 1;
                    }
        
                    &:nth-child(2) {
                        order: 2;
                    }
        
                    &:nth-child(3) {
                        order: 3;
                    }
        
                    &:nth-child(4) {
                        order: 4;
                    }

                    &:nth-child(5) {
                        order: 5;
                    }
                }
                
            }
        }
    }

    &__filter-item {
        display: block;
        width: calc(25% - 20px);
        margin: 0 10px;

        @include below(1240px) {
            width: calc(50% - 20px);
            margin: 20px 10px 0;
        }

        @include below(625px) {
            width: 100%;
            margin: 20px 0 0;

            &:nth-child(1) {
                order: 2;
            }

            &:nth-child(2) {
                order: 3;
            }

            &:nth-child(3) {
                order: 4;
            }

            &:nth-child(4) {
                order: 1;
            }
        }

    }

    &__filter-button {
        width: 100%;
        svg {
            margin-right: 10px;
            width: 24px;
            height: 24px;
        }
    }

    &__filter-input {
        width: 100%;
        height: 54px;
        padding: 15px 20px;
        font-size: 14px;
        font-weight: 400;
        line-height: normal;
        border: 1px solid #CCCCCD;
        transition: border-color .3s linear;

        &:focus,
        &:hover {
            border-color: $black_color;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -20px -10px 0;

        @include below(625px) {
            margin: -20px 0 0;
        }
    }

    &__card {
        width: calc(25% - 20px);
        margin: 20px 10px 0;

        @include below(1380px) {
            width: calc(33.333% - 20px);
        }

        @include below(1240px) {
            width: calc(50% - 20px);
        }

        @include below(625px) {
            width: 100%;
            margin: 20px 0 0;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        margin: 45px auto 0;
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        color: $black_color;
        transition: color .3s linear;

        &:hover {
            color: $accent_color;
        }

        svg {
            margin-left: 10px;
            width: 24px;
            height: 24px;
        }

        @include below(540px) {
            margin: 35px auto 0;
        }
    }
}