@import "main-bx";

.model {
    padding: 0 0 150px;

    @include below(1240px) {
        padding: 20px 0 100px;
    } 
    
    &__title {
        font-size: 35px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 40px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(1024px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 20px;
            text-align: start;
        }
    }

    &__wrap {
        border: 1px solid #CCCCCD;

        @include below(1240px) {
            border: none;
            display: flex;
            flex-wrap: wrap;
            align-items: stretch;
            gap: 32px;
        }

        @include below(735px) {
            gap: 20px;
        }
    }

    &__item {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 60px;


        &:not(:last-child) {
            border-bottom: 1px solid #CCCCCD;
        }

        @include below(1380px) {
            padding: 20px;
        }

        @include below(1240px) {
            flex-direction: column;
            align-items: start;
            border: 1px solid #CCCCCD;
            padding: 20px 20px 30px;
            width: calc(50% - 16px);
        }

        @include below(735px) {
            width: 100%;
        }

        @include below(540px) {
            padding: 15px 15px 30px;
        }
    }

    &__left,
    &__right {
        width: 100%;
        max-width: 330px;

        @include below(1380px) {
            max-width: 300px;
        }
    }

    &__left {
        @include below(1240px) {
            order: 2;
            max-width: 400px;
        }

        @include below(540px) {
            max-width: initial;
        }
    }

    &__right {
        @include below(1240px) {
            order: 3;
            max-width: initial;
        }
    }

    &__img {
        width: 100%;
        max-width: 636px;

        @include below(1240px) {
            order: 1;
            max-width: initial;
        }
    }

    &__name {
        font-weight: 300;
        font-size: 24px;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 24px;

        @include below(1240px) {
            position: absolute;
            top: 15px;
            left: 15px;
            font-size: 20px;
        }

        @include below(768px) {
            font-size: 16px;
        }
    }

    &__configuration {
        &-text {
            font-weight: 400;
            font-size: 11px;
            color: #626970;
            margin-bottom: 10px;
        }

        &-list {
            display: flex;
            flex-wrap: wrap;
            max-width: 220px;

            &-item {
                font-weight: 500;
                font-size: 13px;
                text-transform: uppercase;
            }
        }

        &-btn {
            display: flex;
            align-items: center;
            margin-top: 60px;
            font-weight: 500;
            font-size: 14px;
            line-height: normal;
            color: #121314;
            transition: color .3s linear;

            &:hover {
                color: $accent_color;
            }

            svg {
                margin-left: 10px;
                width: 24px;
                height: 24px;
            }

            @include below(1240px) {
               margin: 25px 0 55px;
            }
        }
    }

    &__choose-btn {
        width: 100%;
    }

    &__price-btn {
        width: 100%;
        margin-top: 15px;
    }


    &__test-drive {
        display: flex;
        align-items: center;
        margin-top: 30px;
        font-weight: 500;
        font-size: 14px;
        line-height: normal;
        color: #121314;
        transition: color .3s linear;

        &:hover {
            color: $accent_color;
        } 

        svg {
            margin-left: 10px;
            width: 24px;
            height: 24px;
        }
    }
}