@import "main-bx";

.card {
    display: flex;
    flex-direction: column;
    padding: 15px;
    border: 1px solid #CCCCCD;
    position: relative;

    &__badge {
        position: absolute;
        top: 15px;
        left: 15px;
        padding: 5px 10px;
        border: 1px solid #000;
        z-index: 10;
        font-size: 9px;
        font-style: normal;
        font-weight: 500;
        line-height: 120%; 
        text-transform: uppercase;
    }

    &__img {
        margin-bottom: 24px;
    }

    &__title {
        font-size: 16px;
        font-weight: 500;
        line-height: 120%;
        text-transform: uppercase;
        margin-bottom: 24px;
    }

    &__body {
        flex: auto;
        display: flex;
        flex-direction: column;
    }

    &__list {
        margin-bottom: 24px;

        &-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 11px;
            font-weight: 400;
            line-height: 120%;
            
            &:not(:last-child) {
                margin-bottom: 5px;
            }

            &_border {
                &:not(:last-child) {
                    margin-bottom: 10px;
                }

                &::after {
                    content: "";
                    display: block;
                    border-bottom: 1px solid #F8F8F8;
                    flex: auto;
                    order: 2;
                    margin: 0 12px;
                }

                &-key {
                    order: 1;
                }

                &-val {
                    order: 3;
                }
            }

        }

        &-key {
            display: flex;
            align-items: center;
            color: #626970;
            order: 1;

            svg {
                margin-right: 8px;
                width: 24px;
                height: 24px;
            }
        }

        &-val {
            order: 3;
        }  
    }

    &__price {
        font-size: 16px;
        font-weight: 500;
        line-height: 120%; 
        text-transform: uppercase;
        margin-bottom: 24px;
    }

    &__btn {
        margin-top: auto;
    }
}