@import "main-bx";

.offers {
    padding: 0 0 150px;   

    @include below(1380px) {
        padding: 0 0 120px;
    }

    @include below(1240px) {
        padding: 0 0 90px;
    } 

    @include below(768px) {
        padding: 0 0 60px;
    } 

    &__title {
        font-size: 35px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 40px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(1024px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 20px;
            text-align: start;
        }
    }

    &__wrap {
        display: flex;
        flex-direction: column;
    }

    &__tabs {
        margin-bottom: 40px;

        @include below(768px) {
            margin-bottom: 20px;
        }
       
        &-list {
            display: flex;
            flex-wrap: wrap;
            margin: -2px -1px 0;

            @include below(1380px) {
                flex-wrap: nowrap;
                overflow: hidden;
                overflow-x: auto;
                padding-bottom: 5px;
                margin: 0;
            }
        }

        &-item {
           
            margin: 2px 1px 0;
            width: calc(20% - 2px);
            &:not(:nth-child(-n+5)) {
                width: calc(25% - 2px);

                @include below(1380px) {
                    width: fit-content;
                    margin: 0 0 0 2px;
                }
            }

            @include below(1380px) {
                width: fit-content;
                margin: 0;
                
                &:not(:first-child) {
                    margin: 0 0 0 2px;
                }
            }
        }

        &-button {
            width: 100%;
            padding: 14px 32px;
            font-size: 14px;
            font-weight: 500;
            line-height: 120%; 
            white-space: nowrap;
            text-transform: uppercase;
            text-align: center;
            color: #626970;
            background-color: #F8F8F8;
            border-bottom: 2px solid transparent;
            transition: border-color .3s linear, color .3s linear;

            &:hover,
            &:active {
                border-color: #000;
                color: #121314;
            }

            &_active {
                border-color: #000;
                color: #121314;
            }
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -20px -10px 0;
        @include below(625px) {
            margin: -20px 0 0;
        }
    }

    &__card {
        width: calc(25% - 20px);
        margin: 20px 10px 0;
        
        @include below(1380px) {
            width: calc(33.333% - 20px);
        }

        @include below(1240px) {
            width: calc(50% - 20px);
        }

        @include below(625px) {
            width: 100%;
            margin: 20px 0 0;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        margin: 45px auto 0;
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        color: $black_color;
        transition: color .3s linear;

        &:hover {
            color: $accent_color;
        }

        svg {
            margin-left: 10px;
            width: 24px;
            height: 24px;
        }

        @include below(540px) {
            margin: 35px auto 0;
        }
    }
}