@import "main-bx";

.header {

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    color: $white_color;
    padding: 10px 30px;

    @include below(575px) {
        padding: 10px 15px;
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__nav {
        @include below(1790px) {
            display: none;
        }
    }

    &__list {
        display: flex;
        margin: 0 -10px;

        &-item {
            margin: 0 10px;
        }

        &-link {
            font-size: 9px;
            font-weight: 500;
            line-height: normal;
            text-transform: uppercase;
            color: $white_color;
            transition: color .3s linear;

            &:hover:not(:active) {
                color: $accent_color;
            }
        }
    }

    &__support {
        position: relative;
        display: flex;
        flex-direction: column;
        text-transform: uppercase;
        padding: 0 18px;
        cursor: pointer;

        @include below(1790px) {
            display: none;
        }

        &:hover {
            .header__info {
                opacity: 1;
                visibility: visible;
            }
        }

        &::before {
            content: "";
            width: 1px;
            height: 100%;
            background-color: rgba(255, 255, 255, 0.2);
            position: absolute;
            top: 0;
            left: 0;
            transform: rotate(30deg);
        }

        &::after {
            content: "";
            width: 1px;
            height: 100%;
            background-color: rgba(255, 255, 255, 0.2);
            position: absolute;
            top: 0;
            right: 0;
            transform: rotate(30deg);
        }

        strong {
            display: inline-block;
            font-size: 16px;
            font-weight: 500;
            line-height: normal;
            text-transform: uppercase;
            margin-bottom: 2px;
        }

        span {
            font-size: 7px;
            font-weight: 500;
            line-height: normal;
        }
    }

    &__info {
        width: 100%;
        max-width: 370px;
        min-width: 370px;
        position: absolute;
        left: 20px;
        top: 40px;
        background-color: rgba(255, 255, 255, 0.9);
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.12);
        padding: 20px;
        color: $black_color;
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s linear, visibility .3s linear;

        &-title {
            text-transform: lowercase;
            font-weight: 500;
            font-size: 16px;
            line-height: normal;
            margin-bottom: 16px;
        }

        &-desc {
            font-weight: 300;
            font-size: 10px;
            line-height: 140%;
            text-transform: none;

            p {
                &:first-child {
                    margin-bottom: 16px;
                }

                &:nth-child(2) {
                    color: $accent_color;
                    margin-bottom: 6px;
                    text-transform: uppercase;
                }

                &:nth-child(3) {
                    color: $accent_color;
                    text-transform: uppercase;
                }
            }
        }
    }

    &__contacts {
        display: flex;
        align-items: center;
        
        &-item {
            position: relative;
            &:first-child {
                margin-right: 34px;
            }

            
            @include below(992px) {
                display: none;
            }
        }
        
        &-row {
            display: flex;
            align-items: center;

            &:hover {
                & ~ .header__contacts-address {
                    visibility: visible;
                    opacity: 1;
                }
            }
        }

        &-text {
            font-weight: 500;
            font-size: 10px;
            line-height: normal;
            text-transform: uppercase;
        }

        &-icon {
            margin: 5px 0 0 6px;
            cursor: pointer;
            color: $white_color;
            svg {
                width: 16px;
                height: 16px;
            }

           
        }

        &-tel {
            display: block;
            font-weight: 300;
            font-size: 10px;
            line-height: normal;
            color: $white_color;
            transition: color .3s linear;

            &:hover:not(:active) {
                color: $accent_color;
            }
        }

        &-address {
            position: absolute;
            top: 40px;
            left: 0;
            right: 0;
            width: 100%;
            padding: 20px;
            background-color: rgba(255, 255, 255, 0.9);
            box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.12);
            color: $black_color;
            font-size: 10px;
            line-height: 14px;
            opacity: 0;
            visibility: hidden;
            transition: opacity .3s linear, visibility .3s linear;
            span {
                font-weight: 500;
                line-height: 120%;
            }

            p {
                font-weight: 300;
                line-height: 140%;
                margin-top: 8px;
            }
        }
    }

    &__pin {
        display: none;
        width: 24px;
        height: 24px;
        color: $white_color;
        transition: color .3s linear;
        &:hover {
            color: $accent_color;
        }
        svg {
            width: 100%;
            height: 100%;
        }

        @include below(992px) {
            display: block;
        }
    }

    &__logo {
        display: block;
        margin-left: 80px;
        width: 100%;
        max-width: 170px;
        height: 50px;
        color: $white_color;
        transition: color .3s linear;
        svg {
            width: 100%;
            height: 100%;
        }

        @include below(1880px) {
            margin-left: 20px;
        }

        @include below(992px) {
            max-width: 140px;
        }

        .header.is-show & {
            @include below(660px) {
                color: #626970;
                position: relative;
                z-index: 300;
            }
        }

        @include below(575px) {
            max-width: 121px;
            height: 36px;
            margin-left: 16px;
        }
    }

    &__burger {
        padding: 6px 4px;
        display: none;

        @include below(1790px) {
            display: block;
            position: relative;
            z-index: 100;
        }

        &.is-change {
            .header__burger-line1 {
                transform: rotate(-45deg) translate(-4px, 2px);
                background-color: #626970;
            }

            .header__burger-line2 {
                opacity: 0;
            }

            .header__burger-line3 {
                transform:  rotate(45deg) translate(-6px, -5px);
                background-color: #626970;
            }
        }
    }

    &__burger-line1,
    &__burger-line2,
    &__burger-line3 {
        display: block;
        width: 25px;
        height: 2px;
        background-color: $white_color;
        border-radius: 1px;
        transition: transform .3s linear, opacity .3s linear, background-color .3s linear;

        @include below(575px) {
            width: 16px;
        }
    }

    &__burger-line1 {
        margin-bottom: 4px;
    }

    &__burger-line2 {
        margin-bottom: 4px;
    }


    &__menu {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 500px;
        height: 100vh;
        padding: 85px 40px 40px;
        background-color: $white_color;
        transform: translateX(-100%);
        visibility: hidden;
        overflow-y: auto;
        z-index: 99;
        display: none;
        transition: transform .3s linear, visibility .3s linear, display .3s linear;
        color: $black_color;

        @include below(1790px) {
            display: block;
        }

        @include below(992px) {
            display: block;

            .header__contacts {
                display: block;
                margin-top: 134px;

                &-item {
                    display: block;

                    &:first-child {
                        margin: 0 0 34px;
                    }
                }

                &-tel {
                    color: $black_color;
                }

                &-text {
                    font-size: 12px;
                    font-weight: 400;
                    line-height: normal;
                }

                &-address {
                    position: static;
                    padding: 0;
                    box-shadow: none;
                    opacity: 1;
                    visibility: visible;

                    p {
                        font-weight: 400;
                        font-size: 10px;
                        line-height: 120%;
                        margin: 8px 0;
                    }
                }
            }
        }

        @include below(660px) {
            max-width: initial;
        }

        @include below(575px) {
            padding: 85px 15px 20px;
        }

        &.is-show {
            visibility: visible;
            transform: translateX(0);
        }

        .header__nav {
            display: block;
            margin-bottom: 40px;
        }

        .header__list {
            display: flex;
            flex-direction: column;
            margin: 0;

            &-item { 
                margin: 0;  
                padding: 10px;
                border-bottom: 1px solid #CCCCCD;
            }

            &-link {
                color: $black_color;
                display: flex;
                align-items: center;
                font-size: 12px;
                line-height: normal;
                text-transform: uppercase;
                svg {
                    margin-left: auto;
                    width: 24px;
                    height: 24px;
                    flex-shrink: 0;
                }
            }
        }

        .header__support {
            display: flex;
            flex-wrap: wrap;
            color: $black_color;
            padding: 0;

            &:hover {
                .header__support-icon {
                    color: $accent_color;
                }
            }

            &::before,
            &::after {
                display: none;
            }

            &-flex {
                display: flex;
                align-items: end;
            }

            &-text {
                display: flex;
                flex-direction: column;
                margin-right: 20px;
            }

            &-icon {
                transition: color .3s linear;
                svg {
                    width: 20px;
                    height: 20px;
                }
            }

            .header__info {
                margin-top: 20px;
                position: static;
                box-shadow: none;
                padding: 0;
                width: 100%;
            }
        }

        .header__contacts {
            display: none;
           
        }

    }

}