@import "main-bx";

.corporate {
    padding: 50px 0 75px;

    @include below(1240px) {
        padding: 40px 0;
    }

    @include below(575px) {
        padding: 20px 0 30px;
    }
    
    &__title {
        font-weight: 500;
        font-size: 35px;
        line-height: 120%; 
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 40px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(1024px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 20px;
            text-align: start;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        margin: -20px -10px 0;
        overflow: hidden;
        @include below(768px) {
            margin: -20px 0 0;
        }
    }

    &__card {
        display: flex;
        flex-direction: column;
        align-items: start;
        width: calc(50% - 20px);
        margin: 20px 10px 0;
        border: 1px solid #CCCCCD;
        padding: 15px;

        @include below(768px) {
            width: 100%;
            margin: 20px 0 0;
        }
    }

    &__img {
        margin-bottom: 32px;

        @include below(768px) {
            margin-bottom: 15px;
        }
    }

    &__caption {
        font-size: 20px;
        font-weight: 500;
        line-height: 120%; 
        text-transform: uppercase;
        margin-bottom: 16px;

        @include below(1024px) {
            font-size: 18px;
        }

        @include below(540px) {
            font-size: 16px;
        }
    }

    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 120%;
        color: #626970;
        margin-bottom: 30px;

        @include below(768px) {
            margin-bottom: 23px;
        }

        span {
            text-transform: uppercase;
        }

        @include below(1024px) {
            font-size: 14px;
        }

        @include below(540px) {
            font-size: 12px;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 14px;
        line-height: normal;
        color: $black_color;
        margin-top: auto;
        transition: color .3s linear;
        &:hover {
            color: $accent_color;
        }

        svg {
            margin-left: 10px;
            width: 24px;
            height: 24px;
        }
    }
}