@import "main-bx";

.btn {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 54px;
    font-size: 14px;
    font-weight: 500;
    line-height: normal;
    color: $white_color;
    padding: 20px 15px;
    background-color: $accent_color;
    text-transform: uppercase;
    -webkit-clip-path: polygon(0 0, 100% 0, 92% 100%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 92% 100%, 0 100%);
    transition: background-color .3s linear;

    @include below(768px) {
        font-size: 12px;
    }

    &:hover:not(:active) {
        background-color: #25DAD0;
    }




    &_black {
        background-color: $black_color;

        &:hover:not(:active) {
            background-color: #2A2B2D;
        }
    }

}