@import "main-bx";

.contacts {
    overflow: hidden;

    padding-bottom: 120px;

    @include below(768px) {
        padding-bottom: 60px;
    }

    &__map {
        width: 100%;
        height: 800px;
        position: relative;
        z-index: 5;

        @include below(1240px) {
            height: 600px;
        }

        @include below(768px) {
            height: 400px;
        }

        @include below(590px) {
            height: 375px;
        }

        .ymaps-2-1-79-copyrights-pane {
            display: none;
        }

        .ymaps-2-1-79-ground-pane {
            filter: grayscale(100%);
            -webkit-filter: grayscale(100%);
            -moz-filter: grayscale(100%);
        }

        .ymaps-2-1-79-balloon {
            position: absolute;
            z-index: 1;
            padding: 1px;
            background: rgba(255, 255, 255, 0.90);
            box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.12);
            backdrop-filter: blur(2px);
        }

        .ymaps-2-1-79-balloon__content {
            position: relative;
            z-index: 1;
            display: block;
            min-width: 60px;
            height: 100%;
            min-height: 15px;
            margin: 0;
            padding: 12px;
            font-family: $accent_font;
        }

        .ymaps-2-1-79-balloon__tail {
            display: none;
        }

        .balloon-content {

            &__address {
                font-size: 12px;
                font-style: normal;
                font-weight: 500;
                line-height: 120%;
                margin-bottom: 16px;
            }

            &__tel {
                font-size: 12px;
                font-weight: 300;
                line-height: normal;
                color: $black_color;
            }
        }

    }

    &__wrap {
        display: flex;
        align-items: stretch;
        margin: -165px 0 -10px;
        position: relative;
        z-index: 100;

        @include below(1240px) {
           flex-wrap: wrap;
           margin: 40px 0 0;
        }

        @include below(590px) {
            margin: 20px 0 0;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        width: calc(50% - 20px);
        margin: 0 10px;
        padding: 30px;
        position: relative;
        background-color: $black_color;
        color: $white_color;

        @include below(1240px) {
            width: 100%;
            margin: 0;
            
            &:last-child {
                margin: 40px 0 0;
            }
        }

        @include below(590px) {
            padding: 20px;

            &:last-child {
                margin: 20px 0 0;
            }
        }

        &-icon {
            position: absolute;
            top: 0;
            right: 60px;

            svg {
                width: 61px;
                height: 54px;
            }

            @include below(1380px) {
                right: 20px;

                svg {
                    width: 54px;
                    height: 44px;
                }
            }

            @include below(590px) {
                svg {
                    width: 36px;
                    height: 32px;
                }
            }
        }

        &-title {
            font-size: 24px;
            font-weight: 500;
            line-height: normal;
            text-transform: uppercase;
            margin-bottom: 14px;

            @include below(1460px) {
                font-size: 22px;
            }

            @include below(1380px) {
                font-size: 20px;
            }

            @include below(768px) {
                font-size: 18px;
            }

            @include below(590px) {
                font-size: 16px;
            }

            @include below(479px) {
                max-width: 270px;
                br {
                    display: none;
                }
            }
        }

        &-address {
            font-size: 14px;
            font-weight: 400;
            line-height: 120%;
            margin-bottom: auto;

            @include below(590px) {
                font-size: 12px;
            }

            @include below(479px) {
                br {
                    display: none;
                }
            }
        }
    }

    &__flex {
        display: flex;
        align-items: stretch;
        margin-top: 50px;

        @include below_and_above(1380px, 1241px) {
            flex-direction: column;
        }

        @include below(668px) {
            flex-direction: column;
        }

        @include below(590px) {
            margin-top: 40px;
        }
    }

    &__col {
        display: flex;
        flex-direction: column;
        &:last-child {
            margin-left: 56px;

            @include below_and_above(1380px, 1241px) {
                margin: 30px 0 0;
            }

            @include below(668px) {
                margin: 40px 0 0;
            }
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        &:first-child {
            margin-bottom: auto;
        }

        &:last-child {
            margin-top: 31px;

            @include below(590px) {
                margin-top: 40px;
            }
        }

        span {
            display: inline-block;
            &:first-child {
                color: rgba(255, 255, 255, 0.50);
                font-size: 14px;
                font-weight: 300;
                line-height: normal;
            }

            &:last-child {
                margin-top: 10px;
                font-size: 20px;
                font-weight: 300;
                line-height: normal;

                @include below(1460px) {
                    font-size: 18px;
                }

                @include below(590px) {
                    font-size: 16px;
                }
            }
        }
    }

    &__tel {
        font-weight: 300;
        font-size: 20px;
        line-height: normal;
        color: $white_color;
        transition: text-decoration .3s linear;

        @include below(1460px) {
            font-size: 18px;
        }

        @include below(590px) {
            font-size: 16px;
        }

        &:hover {
            text-decoration: underline;
        }
    }

    &__mail {
        margin-top: 10px;
        font-weight: 300;
        font-size: 20px;
        line-height: normal;
        color: $white_color;
        transition: text-decoration .3s linear;

        @include below(1460px) {
            font-size: 18px;
        }
        
        @include below(590px) {
            font-size: 16px;
        }

        &:hover {
            text-decoration: underline;
        }
    }
}