@import "main-bx";

.table {

    &__nav {
        display: flex;
        align-items: end;
        justify-content: end;
        margin-bottom: 20px;
        &-btn {
            width: 54px;
            height: 54px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50px;
            background-color: #F8F8F8;
            color: $black_color;
            z-index: 10;

            &:hover:not(:active) {
                background-color: #2A2B2D;
                color: $white_color;
            }

            @include below(768px) {
                width: 44px;
                height: 44px;
            }

            &:first-child {
                margin-right: 12px;
            }

            svg {
                width: 24px;
                height: 24px;
            }
        }
    }

    &__wrap {
        border: 1px solid #CCCCCD;
        border-right: none;
        overflow: hidden;
    }

    &__row {
        display: flex;
        align-items: stretch;
        &:not(:last-child) {
            border-bottom: 1px solid #CCCCCD;
        }
    }

    &__head {
        display: flex;
        align-items: stretch;
        border-bottom: 1px solid #CCCCCD;
    }

    &__col {
        &_small {
            width: 100%;
            max-width: 280px;
            border-right: 1px solid #CCCCCD;

            @include below(1440px) {
                min-width: 200px;
                max-width: 200px;
            }

            @include below(768px) {
                min-width: 120px;
                max-width: 120px;
            }

            @include below(420px) {
                word-wrap: break-word;
                min-width: 100px;
                max-width: 100px;
            }
        }

        &_full {
            width: 100%;
            max-width: 1140px;
            display: flex;

            @include below(1440px) {
                max-width: 80vw;
            }

            @include below(1280px) {
                max-width: 76vw;
            }

            @include below(1100px) {
                max-width: 72.5vw;
            }

            @include below(550px) {
                max-width: 69vw;
            }

            @include below(420px) {
                max-width: 64vw;
            }

            @include below(370px) {
                max-width: 60vw;
            }
        }
    }

    &__label {
        padding: 20px;
        font-size: 14px;
        font-weight: 500;
        line-height: 110%;

        @include below(768px) {
            font-size: 10px;
            padding: 20px 10px;
        }
    }

    &__slider {
        display: flex;
        align-items: stretch;
        width: 100%;

        .slick-track {
            display: flex;
            align-items: stretch;
            left: -4px;

            @include below(1440px) {
                left: -2px;
            }
        }

    }

    &__item {
        padding: 20px;
        width: 100%;
        border-right: 1px solid #CCCCCD;
        @include above(1441px) {
            &:not(:nth-child(-n+5)):not(.slick-slide) {
                display: none;
            }
        }

        @include below_and_above(1440px, 1025px) {
            &:not(:nth-child(-n+4)):not(.slick-slide) {
                display: none;
            }
        }

        @include below_and_above(1024px, 599px) {
            &:not(:nth-child(-n+3)):not(.slick-slide) {
                display: none;
            }
        }
    }

    &__item-title {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #626970;
        font-size: 14px;
        font-weight: 300;
        line-height: 110%;

        @include below(768px) {
            font-size: 12px;
        }
    }

   
    &__params-slider {
        display: flex;
        align-items: stretch;
        width: 100%;

        .slick-list {
            height: 100%;
        }

        .slick-track {
            height: 100%;
            display: flex;
            align-items: stretch;
            left: -4px;

            @include below(1440px) {
                left: -2px;
            }
        }
    }

    &__param {
        padding: 20px;
        width: 100%;
        border-right: 1px solid #CCCCCD;

        &.slick-slide {
            display: flex !important;
            justify-content: center;
            align-items: center;
        }

        @include above(1441px) {
            &:not(:nth-child(-n+5)):not(.slick-slide) {
                display: none;
            }
        }

        @include below_and_above(1440px, 1025px) {
            &:not(:nth-child(-n+4)):not(.slick-slide) {
                display: none;
            }
        }

        @include below_and_above(1025px, 599px) {
            &:not(:nth-child(-n+3)):not(.slick-slide) {
                display: none;
            }
        }

        &-val {
            text-align: center;
            font-size: 12px;
            font-weight: 300;
            line-height: 110%;
        }
    }


    &__text {
        font-size: 12px;
        font-weight: 400;
        line-height: 120%;
        color: #626970;
        margin-top: 20px;

        @include below(768px) {
            font-size: 12px;
        }
    }

}