@import "main-bx";

.promotions {
    position: relative;
    padding: 0 0 150px;


    &__title {
        font-size: 35px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 40px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(1024px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @include below(768px) {
            font-size: 20px;
            margin-bottom: 20px;
            text-align: start;
        }
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }

    &__nav {
        display: flex;
        &-btn {
            width: 54px;
            height: 54px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50px;
            background-color: #F8F8F8;
            color: $black_color;
            z-index: 10;

            &:hover:not(:active) {
                background-color: #2A2B2D;
                color: $white_color;
            }

            &:first-child {
                margin-right: 12px;
            }

            svg {
                width: 24px;
                height: 24px;
            }
        }

        @include below(768px) {
            position: absolute;
            left: 50%;
            bottom: 60px;
            transform: translate(-50%, 0);
        }
    }

    &__link {
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        color: $black_color;
        transition: color .3s linear;

        &:hover {
            color: $accent_color;
        }

        svg {
            margin-left: 10px;
            width: 24px;
            height: 24px;
            flex-shrink: 0;
        }
    }

    &__slider {
        display: flex;
        align-items: stretch;
        .slick-track {
            display: flex;
            align-items: stretch;
        }

        &.slick-initialized .slick-slide {
            display: flex;
            flex-direction: column;
        }

        

        @include below(1460px) {
            .slick-track {
                margin-left: -21%;
            }
        }

        @include below(1240px) {
            .slick-track {
                margin-left: 0%;
            }
        }


        @include below(1024px) {
            .slick-track {
                margin-left: -82.5%;
            }

            &.slick-initialized .slick-slide {
                opacity: 0.4;
            }

            &.slick-initialized .slick-slide.slick-current{
               opacity: 1;
            }
        }
    }

    &__item {
        width: calc(33.333% - 20px);
        margin: 0 10px;

        &.slick-slide {
            display: flex;
            flex-direction: column;
            height: initial;
        }
        &:not(:nth-child(-n+3)):not(.slick-slide) {
            display: none;
        }

        @include below(1460px) {
            width: calc(50% - 20px);
            margin: 0 10px;
    
            &:not(:nth-child(-n+2)):not(.slick-slide) {
                display: none;
            }
        }

        
        @include below(1024px) {
            width: 100%;
            margin: 0 5px;
            &:not(:first-child):not(.slick-slide) {
                display: none;
            }
        }

        .slick-slide {
            display: flex;
            flex-direction: column;
        }
    }

    &__img {

        img {
           
        }
    }

    &__body {
       flex: auto;
       margin-bottom: 15px;
    }

    &__content {
        height: 100%;
        padding: 20px;
        background-color: #F8F8F8;
        display: flex;
        flex-direction: column;
        color: #000;

        @include below(500px) {
            padding: 10px;
        }

        &-title {
            font-size: 14px;
            font-weight: 500;
            line-height: 130%;
            margin-bottom: 10px;
        }

        &-text {
            font-size: 10px;
            font-weight: 400;
            line-height: 140%;

            @include below(500px) {
                -webkit-line-clamp: 3; 
                display: -webkit-box; 
                -webkit-box-orient: vertical;
                overflow: hidden;
            }
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        margin-top: auto;

        &-link {
            display: flex;
            align-items: center;
            margin-left: 24px;
            font-size: 14px;
            font-weight: 500;
            line-height: normal;
            color: $black_color;
            transition: color .3s linear;

            @include below(479px) {
                margin-left: 12px;
            }
    
            &:hover {
                color: $accent_color;
            }

            &::before {
                content: "";
                width: 1px;
                height: 32px;
                display: block;
                background-color: #CCCCCD;
                transform: rotate(30deg);
            }

            span {
                margin-left: 24px;

                @include below(479px) {
                    margin-left: 12px;
                }
            }
    
            svg {
                margin-left: 10px;
                width: 24px;
                height: 24px;
            }
        }
    }

    &__icon {
        cursor: pointer;
        width: 30px;
        height: 30px;
        color: $black_color;
        transition: color .3s linear;

        &:hover {
            color: $accent_color;
        }

        svg {
            width: 100%;
            height: 100%;
        }
    }

}