@import "main-bx";

.accordion {

    &:not(.is-active) {
        display: none;
    }

    &__item {
        border-top: 1px solid #CCCCCD;

        &:last-child {
            border-bottom: 1px solid #CCCCCD;
        }
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0;
        cursor: pointer;

        &_active {
            .accordion__btn {
                transform: rotate(180deg);
            }

            & ~ .accordion__content {
                margin-bottom: 20px;

                @include below(768px) {
                    margin-bottom: 15px;
                }
            }
        }

        @include below(768px) {
            padding: 15px 0;
        }
    }

    &__title {
        font-size: 22px;
        font-weight: 500;
        text-transform: uppercase;
        color: #121314;

        @include below(768px) {
            font-size: 16px;
        }
    }

    &__btn {
        transition: transform .3s linear;
        svg {
            width: 24px;
            height: 24px;

            @include below(768px) {
                width: 20px;
                height: 20px;
            }
        }
    }

    &__list {
        list-style-type: disc;
        padding-left: 20px;

        @include below(540px) {
            padding-left: 15px;
        }

        &-item {
            padding-left: 10px;
            font-size: 14px;
            font-weight: 400;
            &:not(:last-child) {
                margin-bottom: 8px;
            }

            @include below(540px) {
                padding-left: 5px;
                font-size: 12px;

                &:not(:last-child) {
                    margin-bottom: 12px;
                }
            }
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        opacity: 0;
        transition: max-height .3s linear, opacity .3s linear, overflow .3s linear;
    }
}