@import "main-bx";

.footer {
    background-color: $black_color;
    color: $white_color;
    padding: 24px 0 40px;

    @include below(575px) {
        padding: 24px 0 15px;
    }


    &__row {
        display: flex;
        justify-content: space-between;
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);

        @include below(1240px) {
            flex-direction: column;
            padding-bottom: 30px;
        }
    }

    &__left {

        @include below(1240px) {
            margin-bottom: 60px;
        }

        @include below(575px) {
            margin-bottom: 40px;
        }

        &-title {
            font-weight: 300;
            font-size: 12px;
            line-height: normal;
            margin-bottom: 12px;
            color: rgba(255, 255, 255, 0.5)
        }

        &-text {
            display: flex;
            flex-direction: column;
            font-size: 14px;
            font-style: normal;
            font-weight: 300;
            line-height: 140%;

            @include below(590px) {
                font-size: 12px;
            }

            span {
                display: inline-block;
            }

            a {
                color: $white_color;
            }
        }
    }

    &__right {
        display: flex;
    }

    &__flex {
        display: flex;
        flex-direction: column;
        align-items: end;

        @include below(1240px) {
            align-items: start;
        }
    }

    &__social {
        display: flex;
        margin: 0 -6px;

        @include below(575px) {
            margin: 0 -4px;
        }

        &-link {
            display: block;
            margin: 0 6px;
            width: 50px;
            height: 50px;
            transition: fill .3s linear;

            &:hover {
                svg {
                    fill: #25DAD0;
                }
            }
       

            svg {
                width: 100%;
                height: 100%;
                fill: #00CABF;
                transition: fill .3s liner;  
            }

            @include below(575px) {
                margin: 0 4px;
                width: 44px;
                height: 44px;

            }
        }
    }

    &__apps {
        display: flex;
        margin-top: 30px;

        @include below(575px) {
            flex-direction: column;
            margin-top: 12px;
        }
       
        &-item {
            width: 168px;
            height: 50px;
            transition: fill .3s linear;

            &:hover {
                svg {
                    fill: #2A2B2D;
                }
            }

            &:first-child {
                margin-right: 12px;
            }
            svg {
                width: 100%;
                height: 100%;
                fill: #121314;
                transition: fill .3s liner;
            }

            @include below(575px) {
                width: 148px;
                height: 44px;

                &:first-child {
                    margin: 0 0 6px;
                }
            }
        }
    }

    &__qrcode {
        margin-left: 24px;
        width: 100%;
        max-width: 130px;
        flex-shrink: 0;

        @include below(575px) {
            max-width: 150px;
            margin-left: 47px;
        }
        img {
            
        }
    }

    &__wrap {

    }


    &__top {
        display: flex;
        align-items: center;
        padding: 32px 0 24px;

        &_active {
            .footer__top-icon {
                transform: rotate(0deg);
                margin-top: 5px;
            }
        }

        &-title {
            font-size: 14px;
            font-weight: 500;
            line-height: normal;
            margin-right: 10px;
        }

        &-icon {
            
            color: $white_color;
            transform: rotate(-180deg);
            transition: transform .3s linear;
            svg {
                width: 24px;
                height: 24px;
            }
        }

    }

    &__content {

        max-height: 0;
        overflow: hidden;
        opacity: 0;
        transition: max-height .3s linear, opacity .3s linear, overflow .3s linear;

        &-text {
            font-size: 12px;
            font-weight: 400;
            line-height: 140%;
            color: rgba(255, 255, 255, 0.4);

            p {
                &:not(:last-child) {
                    margin-bottom: 15px;
                }
            }
        }      
    }

    &__policy {
        font-size: 14px;
        font-weight: 400;
        line-height: 140%;
        margin-top: 32px;
        color: rgba(255, 255, 255, 0.7);

        @include below(590px) {
            font-size: 12px;
        }

        a {
            color: rgba(255, 255, 255, 0.7);
            text-decoration: underline;
        }
    }
}
